.carousel_wrapper {
    display: flex;
    flex-direction: row;
    justify-content: center;
    color: aliceblue;
    width: 70%;
    height: 400px;
    /* border: 3px solid palevioletred; */
}

.carousel_main_wrapper {
    display: flex;
    flex-direction: column;
    width: 95%;
    height: 100%;
    /* border-radius: 40px; */
    /* border: 1px solid blue; */
    /* height: 90%; */
}

.carousel_main_content_wrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    height: 80%;
}

.carousel_controllers_wrapper {
    display: flex;
    flex-direction: column;
    /* align-items: center; */
    justify-content: center;
    width: 5%;
}

.carousel_main_content_wrapper .figure_wrapper {
    display: flex;
    /* justify-content: center; */
    /* height: 100%; */
    width: 100%;
    background-color: antiquewhite;
    /* border: 2px solid red; */
}

.carousel_main_content_wrapper .figure_wrapper img {
    /* display: flex; */
    width: 100%;
    object-fit: contain !important;
}

.carousel_caption_wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 20%;
    background-color: antiquewhite; /* #273746 */
    /* width: 100%; */
    /* padding: 20px; */
    /* border: 1px solid yellowgreen; */
}

.carousel_caption_wrapper a {
    color: #273746;
    font-size: 18px;
    font-weight: 500;
    width: 90%;
}

.carousel_next_previous {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 30px;
    background-color: #34495E;
    text-align: center;
    /* height: 100%; */
    /* border: 1px solid yellowgreen; */
}

.carousel_next_previous:hover {
    background-color: #2E4053;
}

.carousel_buttons:hover, .carousel_next_previous:hover {
    cursor: pointer;
}